import { Component, createRef } from 'react';
import { Flipper, Flipped } from 'react-flip-toolkit';
import './RemoveItem.scss';
import { GadgetWindow } from './WindowElem';

export class DeleteButton extends Component {
  state = {
  //       
  //    this.props ..     
  //    ,  -  .
    isOpen: this.props.isOpen,
    // isOpen: false,
  };
  //         React createRef()
  ref = createRef();

  //       
  toggleState = () => {
    //     true   
    if (this.state.isOpen) return;
    this.setState(prev => ({
      isOpen: !prev.isOpen,
    }));
    //    
    this.ref.current.focus();
  };

  keydownHandler = event => {
    //      
    //      'Enter' || 'Escape'
    if (event.key === 'Enter' || event.key === 'Escape') {
      this.toggleState();
    }
  };

  handleClick = () => this.setState({ isOpen: false });
//      Delete     
//       id 
  handleDelete = () => {
    this.props.onDelete(this.props.id); // id   this.props  
    this.setState({ isOpen: false });
  }
  
  render() {
    // console.log(this.props.text);

    //    
    const { isOpen } = this.state;
    const { ref, keydownHandler, toggleState, handleClick, handleDelete } = this;
    return (
      <Flipper flipKey={isOpen} spring="stiff" stagger>
       
        {isOpen ? (
          <GadgetWindow
            //           -
            unitRef={ref}
            isOpen={isOpen}
            toggleState={toggleState}
            keydownHandler={keydownHandler}
            handleClick={handleClick}
            // textObj={this.props.text}
            //      Delete
            handleDelete={handleDelete}
          />
        ) : (
          <Flipped flipId="wrapper">
            <div
              ref={ref}
              tabIndex={0}
              role="button"
              className="button primary"
              onClick={toggleState}
              onKeyDown={keydownHandler}
            >
              <Flipped flipId="action">
                <span className="action">Delete</span>
              </Flipped>
            </div>
          </Flipped>
         )}
      </Flipper>
    );
  }
}
